import React from 'react';
import {
    Cell,
    Group, List,
    Panel, PanelHeader,
    Root,
    View
} from '@vkontakte/vkui';
import Films from "./panels/Films";
import Cartoons from "./panels/Cartoons";
import {connect} from "react-redux";
import {activeArticleAC, activeCategoryAC, activeFilterAC} from "./reducers/MainReducer";
import Preview from "./panels/Preview";


const dataApp = (props) => {
    return (
        <Root activeView={props.main.activeArticle} >
            <Films setActiveArticle={props.setActiveArticle} setActiveFilter={props.setActiveFilter} setActiveCategory={props.setActiveCategory} id="films" state={props.main} />
            <Preview setActiveArticle={props.setActiveArticle} setActiveFilter={props.setActiveFilter}  state={props.main} id="preview"/>
        </Root>
    )
};



let mapStateToProps = (state) => {
    return {
        main: state.main
    };
};

let mapDispatchToProps = (dispatch) => {
    return {
        setActiveArticle: (article) => {
            dispatch(activeArticleAC(article))
        },
        setActiveFilter: (activeFilter) => {
            dispatch(activeFilterAC(activeFilter))
        },
        setActiveCategory: (activeCategory) => {
            dispatch(activeCategoryAC(activeCategory))
        },
    };
};

const App = connect(mapStateToProps, mapDispatchToProps)(dataApp);

export default App;
